








































































import { Component, Prop, Vue } from "vue-property-decorator";
import jobApi from "@/api/jobApi";
import { JobModel } from "@/model/jobModel";

@Component
export default class JobDetailCustom extends Vue {
  rt: any = {
    jobId: ""
  };
  job: JobModel = {
    config: {}
  };
  // Job Instance List
  jobInstanceListColumns: object[] = [
    {
      type: "expand",
      key: "id",
      width: 50
    },
    {
      type: "selection",
      title: "ID",
      key: "id",
      width: 50
    },
    {
      title: "ID",
      key: "id",
      align: "center",
      width: 100
    },
    {
      title: "名称",
      key: "name",
      align: "center"
    },
    {
      title: "类型",
      key: "type",
      align: "center"
    },
    {
      title: "描述",
      key: "description",
      align: "center"
    },
    {
      title: "开始时间",
      key: "start_time",
      align: "center"
    },
    {
      title: "结束时间",
      key: "stop_time",
      align: "center"
    },
    {
      title: "状态",
      key: "last_status",
      align: "center"
    },
    {
      title: "操作",
      fixed: "right",
      align: "center",
      slot: "operator",
      width: 100
    }
  ];
  jobInstanceList: object[] = [
    {
      id: 10001,
      name: "作业1",
      type: "自定义 / Jar",
      description: "单词统计",
      start_time: "2020-01-13 12:14:00",
      stop_time: "2020-01-13 14:45:21",
      last_status: "已停止",
      _checked: true
    },
    {
      id: 10002,
      name: "作业2",
      type: "自定义 / Jar",
      description: "文件测试",
      start_time: "2020-01-13 12:16:00",
      stop_time: "2020-01-13 12:45:21",
      last_status: "已停止"
    }
  ];
  clickStart() {
    this.$Message.success("启动");
  }
  clickRestart() {
    this.$Message.success("重启");
  }
  clickStop() {
    this.$Message.success("停止");
  }
  clickDelete() {
    this.$Message.success("删除");
  }
  clickEdit() {
    this.$router.push({
      path: "/job/edit",
      query: {
        id: this.rt.jobId
      }
    });
  }
  // get
  getJob() {
    jobApi.queryJob({ jobId: this.rt.jobId }).then((res: any) => {
      this.job = res;
    });
  }

  parseRouter() {
    let id = this.$route.query.id;
    this.rt.jobId = id;
  }

  mounted() {
    this.parseRouter();
    this.getJob();
  }
}
